import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import { ReceiptAmountState, AmountConstants, AmountType } from '../amount/models';
import { ShelfState } from '../shelf/models';
import { RouteComponentProps, withRouter } from 'react-router';
import GridList from 'material-ui/GridList';
import GridListTile from 'material-ui/GridList/GridListTile';
import GridListTileBar from 'material-ui/GridList/GridListTileBar';
import UpIcon from 'material-ui-icons/KeyboardArrowUp';
import DownIcon from 'material-ui-icons/KeyboardArrowDown';
import IconButton from 'material-ui/IconButton';
import purple from 'material-ui/colors/purple';
import Typography from 'material-ui/Typography';
import Messages from '../amount/Messages';

const styles: StyleRules< 'receipt' | 'tile' | 'top' > = {
    top: {
        width: '100%'
    },
    receipt: {
        marginTop: 10,
        marginLeft: 10,
        marginBottom: 10
    },
    tile: {
        height: '100%',
        width: '100%',
        display: 'flex',
        alignItems: 'flex-end',
        justifyContent: 'center',
        backgroundColor: purple[200]
    },
};

type ClassNames = keyof typeof styles;

interface ReceiptAmountProps {
}

class ReceiptAmountContainer extends React.Component<Idispatcher &
                                            ReceiptAmountProps &
                                            ReceiptAmountState &
                                            ShelfState & 
                                            RouteComponentProps<any>&
                                            WithStyles<ClassNames>, 
                                            {}> {

    increment = (type: AmountType) => {
        this.props.increamentCoin(type);
    }

    decrement = (type: AmountType) => {
        this.props.decrementCoin(type);
    }

    render(): JSX.Element {
        const {classes, CoinCounters, ReceiptAmount, TotalPayment} = this.props;
        let message = Messages.enough;
        if (ReceiptAmount < TotalPayment) {
            message = Messages.notEnough;
        } else if ( ReceiptAmount === TotalPayment) {
            message = Messages.just;
        }
        return (
        <div className={classes.top}>
            <div className={classes.receipt}>
            <Typography type="title" color="inherit" >
                あずかり　{ReceiptAmount}えん 
            </Typography>
            <Typography type="title" color="inherit" >
                そうがく　{TotalPayment}えん 
            </Typography>
            <Typography type="body1" color="inherit" >
                {message}
            </Typography>
            </div>
            <div >
            <GridList cols={3} cellHeight={120} >
            {AmountConstants.sort((a, b) => (a.value - b.value))
                            .map((tile) => {
                const counter = CoinCounters.find((obj) => (obj.type === tile.type));
                let count = 0;
                if (counter) {
                    count = counter.count;
                }
                return (
                    <GridListTile 
                        
                        key={tile.type}
                    >
                        <GridListTileBar
                            titlePosition="top"
                            title={tile.type + 'えん'}
                            subtitle={count + 'まい'}
                        />
                        <div className={classes.tile}>
                            <IconButton onClick={this.increment.bind(this, tile.type)}>
                                <UpIcon color="error"/>
                            </IconButton>
                            <IconButton onClick={this.decrement.bind(this, tile.type)}>
                                <DownIcon color="error"/>
                            </IconButton>
                        </div>
                    </GridListTile>
                );
                })
            }
            </GridList>
            </div>
        </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            ReceiptAmountState & ShelfState & RouteComponentProps<any> 
                            & ReceiptAmountProps>(ReceiptAmountContainer);

export default withRouter(StyledLiveContainer);


