import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import Live from '../container/live/Index';
import MoneyInput from '../container/moneyInput/Index';
import ReceiptAmount from '../container/amount/Index';
import purple from 'material-ui/colors/purple';
import green from 'material-ui/colors/green';
import Tabs, { Tab } from 'material-ui/Tabs';
import InputNumber from 'material-ui-icons/Apps';
import PhotoCamera from 'material-ui-icons/PhotoCamera';

const styles: StyleRules<'root'| 'live' | 'amount'> = {
    root: {
        width: '100%',
        display: 'flex',
        alignItems: 'stretch',
        alignContent: 'stretch'
    },
    live: {
        width: '60%',
        backgroundColor: purple[800],
    },
    amount: {
        width: '40%',
        backgroundColor: green[800]
    }
};

type ClassNames = keyof typeof styles;

interface PaymentComponentProps {
}

interface ComponentState {
    height: number;
    tabIndex: number;
}

class PaymentComponent extends React.Component<PaymentComponentProps & WithStyles<ClassNames>, 
    ComponentState> {
    private resizeHandler: () => void;

    constructor(props: any) {
        super(props);
        this.state = {
            height: window.innerHeight,
            tabIndex: 0
        };
        this.resizeHandler = () => {
            this.setState({
               height: window.innerHeight 
            });
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.resizeHandler);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resizeHandler);
    }

    tabChanged = (e: any, tabIndex: any) => {
        this.setState({
            tabIndex
        });
    }

    render() {
        const {classes} = this.props;
        const {tabIndex, height} = this.state;
        return (
        <div className={classes.root}>
                <div 
                    className={classes.live}
                    style={{minHeight: height}}
                >
                <Tabs 
                        id="tabs_element_id"
                        indicatorColor="primary"
                        textColor="primary"
                        value={tabIndex}
                        centered={true}
                        onChange={this.tabChanged}
                >
                    <Tab icon={<InputNumber/>} />
                    <Tab icon={<PhotoCamera />} />
                </Tabs>
                {tabIndex === 0 && <MoneyInput />}
                {tabIndex === 1 && <Live />}
                </div>
                <div 
                    className={classes.amount}
                    style={{minHeight: this.state.height}}
                >
                    <ReceiptAmount />
                </div>
        </div>
        );
    }
}

export default withStyles<{} & ClassNames>(styles)<PaymentComponentProps>(PaymentComponent);


