import { createAction } from 'redux-actions';
import * as AT from '../../constants/ActionTypes';
import { OCRrecognizing } from './models';

export const ocrReady = createAction(
  AT.OCR_READY
);

export const ocrRecognizing = createAction<OCRrecognizing, boolean>(
  AT.OCR_RECOGNIZING,
  (recognizing: boolean) => ({ recognizing })
);


