interface IdomainConstants {
    home: string;
    pay: string;
}

class DomainConstants implements IdomainConstants  {
    public home = '/learnrezi';
    public pay = this.home + '/pay';
    public goods = this.home + '/goods';
    public addGoods = this.home + '/add';
}

export default new DomainConstants();


