import { createAction } from 'redux-actions';
import { Media, DeviceIds, DeviceIndex } from './models';
import * as AT from '../../constants/ActionTypes';

export const SetStream = createAction<Media, MediaStream>(
  AT.SET_MEDIA_STREAM,
  (stream: MediaStream) => ({ stream })
);

export const SetDeviceIds = createAction<DeviceIds, string[]>(
  AT.SET_MEDIA_DEVICE_IDS,
  (deviceIds: string[]) => ({ deviceIds })
);

export const SetDeviceIndex = createAction<DeviceIndex, number >(
  AT.SET_DEVICE_INDEX,
  (deviceIndex: number) => ({ deviceIndex })
);


