import { IrootState } from '../../reducers/Root';
import { Dispatch } from 'react-redux';
import { SetStream, SetDeviceIds, SetDeviceIndex } from './LiveActions'; 
import { connect } from 'react-redux';
import LiveContainer from './Live';
import { LiveState } from './models';
import OCR from '../ocr/Index';

export interface Idispatcher {
    fetchDeviceIds(): void;
    startLive(video: HTMLVideoElement, canvas: HTMLCanvasElement): void;
    changeDevice(video: HTMLVideoElement): void;
    startAnalysis(canvas: HTMLCanvasElement, video: HTMLVideoElement): void;
}

class DispatcherFactory {

    private dispatch: Dispatch<IrootState>;
    private state: LiveState;
    private ocr: OCR;

    set DispatchFunc(d: Dispatch<IrootState>) {
        this.dispatch = d;
        this.ocr = new OCR(d);
    }

    get DispatchFunc(): Dispatch<IrootState> {
        return this.dispatch ;
    }
    
    set State(st: LiveState) {
        this.state = st;
    }

    get State(): LiveState {
        return this.state;
    }

    binalizeStart(video: HTMLVideoElement, canvas: HTMLCanvasElement) {
        const loop = () => {
            this.binalizeProcess(video, canvas);
            requestAnimationFrame(loop);
        };
        requestAnimationFrame(loop);
    }

    binalizeProcess(video: HTMLVideoElement, canvas: HTMLCanvasElement) {
            const context = canvas.getContext('2d');
            const videoWidth = video.videoWidth;
            const videoHeight = video.videoHeight;
            if (videoWidth === 0) {
                return ;
            }
            canvas.width = videoWidth; 
            canvas.height = videoHeight;
            if (context) {
                context.drawImage(video, 0, 0);
                const src = context.getImageData(0, 0, videoWidth, videoHeight);
                const dst = context.createImageData(videoWidth, videoHeight);
                const aveList = [];
                let ave = 0;
                let aveCount = 0;
                // 平均輝度の取得
                for (let i = 0; i < src.data.length; i = i + 4) {
                    const y = Math.floor(0.299 * src.data[i] + 0.587 * src.data[i + 1] + 0.114 * src.data[i + 2]);
                    dst.data[i] = dst.data[i + 1] = dst.data[i + 2] = y;
                    dst.data[i + 3] = src.data[i + 3];
                    ave += y;
                    aveCount++;
                    if (aveCount === 1000) {
                        aveList.push(ave / aveCount);
                        ave = 0;
                        aveCount = 0;
                    }
                }
                const average = Math.floor(aveList.reduce(
                    (pre: number, current: number) => { return pre + current; })
                    / aveList.length) + 125;
                // 二値化
                for (let i = 0; i < src.data.length; i = i + 4) {
                    const y = dst.data[i];
                    const bin = y > average ? 255 : 0;
                    dst.data[i] = dst.data[i + 1] = dst.data[i + 2] = bin;
                    dst.data[i + 3] = src.data[i + 3];
                }
                context.putImageData(dst, 0, 0);
            }
    }

    async startStreaming(video: HTMLVideoElement, mediaStream: MediaStream|null, deviceId: string) {
            if (mediaStream) {
                mediaStream.getTracks().forEach((track: MediaStreamTrack) => {
                    track.stop();
                });
            }
            video.autoplay = true;
            const constraint = {
                video: {deviceId}
            };
            const stream =  await navigator.mediaDevices.getUserMedia(constraint);
            video.srcObject = stream;
            this.dispatch(SetStream(stream)); 
    }

    createDispathcer(): Idispatcher {
        const self = this;
        return {
            fetchDeviceIds : async (): Promise<string[]> => {
                const deviceInfos = await navigator.mediaDevices.enumerateDevices();
                const deviceIds = [];
                for (var i = 0; i !== deviceInfos.length; ++i) {
                    const deviceInfo = deviceInfos[i];
                    if (deviceInfo.kind === 'videoinput') {
                        deviceIds.push(deviceInfo.deviceId);
                    }
                }
                self.dispatch(SetDeviceIds(deviceIds));
                return deviceIds;
            },
            startAnalysis : (canvas: HTMLCanvasElement, video: HTMLVideoElement) => {
                self.ocr.startAnalysis(canvas);
            },
            startLive: async (video: HTMLVideoElement, canvas: HTMLCanvasElement) => {
                const state = self.state;
                self.startStreaming(video, state.mediaStream, state.deviceIds[state.selectedDeviceIdIndex]);
                self.binalizeStart(video, canvas);
            },
            changeDevice: (video: HTMLVideoElement) => {
                const state = self.state;
                let nextIndex = state.selectedDeviceIdIndex + 1;
                if (nextIndex >= state.deviceIds.length) {
                    nextIndex = 0;
                }
                self.startStreaming(video, state.mediaStream, state.deviceIds[nextIndex]);
                self.dispatch(SetDeviceIndex(nextIndex)); 
            }
        };
    }
}

const dispatcher = new DispatcherFactory();

export default connect((st: IrootState) => {
                            dispatcher.State = st.Live; 
                            return Object.assign(st.OCR, st.Live); }, 
                       (dispatch: Dispatch<IrootState>) => { 
                        dispatcher.DispatchFunc = dispatch;
                        return dispatcher.createDispathcer() as Idispatcher; })
                        (LiveContainer);


