import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import SwitchCamera from 'material-ui-icons/SwitchCamera';
import { LiveState } from './models';
import { Idispatcher } from './Index';
import IconButton from 'material-ui/IconButton/IconButton';
import purple from 'material-ui/colors/purple';
import CameraIcon from 'material-ui-icons/Camera';
import { OCRState } from '../ocr/models';
import Dialog from 'material-ui/Dialog';
import DialogTitle from 'material-ui/Dialog/DialogTitle';
import DialogContent from 'material-ui/Dialog/DialogContent';
import { CircularProgress } from 'material-ui/Progress';

const styles: StyleRules<'readyDialog' | 'top' | 'leftActionBar' | 'rightActionBar' | 'liveActionGroup'> = {
    top: {
        width: '100%',
        alignItems: 'center',
        position: 'relative',
        boxSizing: 'border-box'
    },
    readyDialog: {
        display: 'flex',
        justifyContent: 'center'
    },
    leftActionBar: {
        position: 'absolute',
        left: 10,
        top: 0,
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        boxSizing: 'border-box'
    },
    rightActionBar: {
        position: 'absolute',
        right: 10,
        top: 0,
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        boxSizing: 'border-box'
    },
    liveActionGroup: {
        display: 'flex',
        backgroundColor: purple[100],
        flexDirection: 'column',
        borderRadius: '50%'
    }
};

type ClassNames = keyof typeof styles;

interface LiveContainerProps {
}

interface LiveComponentState {
    canvasHeight: number;
}

class LiveContainer extends React.Component<Idispatcher &
                                            LiveState &
                                            LiveContainerProps &
                                            OCRState &
                                            WithStyles<ClassNames>, 
                                            LiveComponentState> {

    constructor(props: any) {
        super(props);
        this.state = {
            canvasHeight: window.innerHeight
        };
    }

    async componentDidMount() {
        await this.props.fetchDeviceIds();
        const live = document.getElementById('live') as HTMLVideoElement;
        const canvas = document.getElementById('capture') as HTMLCanvasElement;
        await this.props.startLive(live, canvas);
        this.calcCanvasHeight();
        window.addEventListener('resize', this.calcCanvasHeight);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.calcCanvasHeight);
    }

    changeCamera = async () => {
        const live = document.getElementById('live') as HTMLVideoElement;
        this.props.changeDevice(live);
    }

    recognize = () => {
        const live = document.getElementById('live') as HTMLVideoElement;
        const canvas = document.getElementById('capture') as HTMLCanvasElement;
        this.props.startAnalysis(canvas, live);
    }

    calcCanvasHeight = () => {
            const tabElement = document.getElementById('tabs_element_id');
            if (tabElement) {
                const height = (window.innerHeight - (tabElement.clientHeight * 2) + 10);
                this.setState({
                    canvasHeight: height
                });
            }
    }

    render(): JSX.Element {
        const {classes, OcrReady, Recognizing} = this.props;
        return (
        <div className={classes.top}>
            <canvas id="capture" style={{width: '100%', height: this.state.canvasHeight}}/>
            <video id="live" src="" style={{display: 'none'}}/>
            <div className={classes.leftActionBar}>
                <div className={classes.liveActionGroup}>
                <IconButton onClick={this.changeCamera}>
                    <SwitchCamera />
                </IconButton>
                </div>
            </div>
            <div className={classes.rightActionBar}>
                <div className={classes.liveActionGroup}>
                <IconButton  onClick={this.recognize} >
                    <CameraIcon />
                </IconButton>
                </div>
            </div>
            <Dialog open={!OcrReady}>
                <DialogTitle>
                    カメラ準備中
                </DialogTitle>
                <DialogContent className={classes.readyDialog}>
                    <CircularProgress />
                </DialogContent>
            </Dialog>
            <Dialog open={Recognizing}>
                <DialogTitle>
                    勘定中
                </DialogTitle>
                <DialogContent className={classes.readyDialog}>
                    <CircularProgress />
                </DialogContent>
            </Dialog>
        </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            LiveState &
                            OCRState &
                            LiveContainerProps>(LiveContainer);

export default StyledLiveContainer;


