import { Dispatch } from 'redux';
import { IrootState } from '../../reducers/Root';
import { Tesseract } from 'tesseract.ts';
import { recivedMoney } from '../amount/actions';
import { ocrReady, ocrRecognizing } from './actions';

class Ocr {
    private dispatcher: Dispatch<IrootState>;

    constructor(dispatcher: Dispatch<IrootState>) {
        this.dispatcher = dispatcher;
    }

    doAnalysis(canvas: HTMLCanvasElement) {
        this.dispatcher(ocrRecognizing(true));
        Tesseract.recognize(canvas, {lang: 'eng', tessedit_char_whitelist: '015'}).then((result) => {
            const strList = result.words.map((r) => { return r.text; });
            this.dispatcher(recivedMoney(strList));
            this.dispatcher(ocrRecognizing(false));
        });
    }

    startAnalysis(canvas: HTMLCanvasElement) {
        this.doAnalysis(canvas);
    }

    prepare() {
        const canvas = document.createElement('canvas') as HTMLCanvasElement;
        const ctx = canvas.getContext('2d');
        canvas.width = 320;
        canvas.height = 120;
        if (ctx) {
            ctx.fillText('111111', 15, 175);
        }
        Tesseract.recognize(canvas, {lang: 'eng', tessedit_char_whitelist: '015'}).then((result) => {
            this.dispatcher(ocrReady());
        });
    }
}

export default Ocr;


