import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import StoreShelf from '../container/shelf/Index';
import ShoppingBasket from '../container/basket/Index';
import purple from 'material-ui/colors/purple';
import green from 'material-ui/colors/green';

const styles: StyleRules<'price' |  'top' | 'shelf' | 'basket' > = {
    top: {
        width: '100%',
        display: 'flex',
        alignItems: 'stretch',
        alignContent: 'stretch'
    },
    price: {
        marginRight: 30
    },
    shelf: {
        width: '60%',
        backgroundColor: purple[800],
        overflowY: 'scroll'
    },
    basket: {
        width: '40%',
        backgroundColor: green[800]
    }
};

type ClassNames = keyof typeof styles;

interface CatalogComponentProps {
}

interface ComponentState {
    height: number;
}

class CatalogComponent extends React.Component<CatalogComponentProps & WithStyles<ClassNames>, ComponentState> {

    private resizeHandler: ( ) => void;

    constructor(props: any) {
        super(props);
        this.state = {
            height: window.innerHeight
        };
        this.resizeHandler = () => {
            this.setState({
               height: window.innerHeight 
            });
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.resizeHandler);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resizeHandler);
    }

    render() {
        const {classes} = this.props;
        return (
        <div className={classes.top} >
            <div className={classes.shelf} style={{minHeight: this.state.height}}>
                <StoreShelf  />
            </div>
            <div className={classes.basket} style={{minHeight: this.state.height}}>
                <ShoppingBasket />
            </div>
        </div>
        );
    }
}

export default withStyles<{} & ClassNames>(styles)<CatalogComponentProps>(CatalogComponent);


